<template>
<div id="barreiro">
      <div class="container-restaurant">
      <div class="restaurant--wrap">
          <ul class="restaurant__list" v-for="restaurant in restaurants"  :key="restaurant.id">
            <li class="restaurant__list__unique"  v-if="restaurant.location === 'Barreiro'">
              <div class="restaurant-text-container">
                <span class="restaurant__list__unique__text"  v-if="restaurant.name">
                  <p>Nome: </p> {{restaurant.name}}
                </span>
                <span class="restaurant__list__unique__text" v-if="restaurant.price">
                  <p>Preço: </p> {{restaurant.price}}
                </span>
              </div>
                <router-link v-bind:to="{name: 'view-restaurant', params: {name: restaurant.name}}">
                  <div class="view-restaurant-btn">
                    Ver Restaurante
                  </div>
                </router-link>
            </li>
          </ul>
      </div>
      <router-link class="back-link" to="/">
        <div class="back-btn">Home</div>
      </router-link>
    </div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'barreiro',
  data () {
    return {
      restaurants: []
    }
  },
  created () {
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.data())
        const data = {
          'id': doc.id,
          'restaurant_id': doc.data().restaurant_id,
          'name': doc.data().name,
          'location': doc.data().location,
          'obs': doc.data().obs,
          'price': doc.data().price,
          'zomato_link': doc.data().zomato_link
        }
        this.restaurants.push(data)
      })
    })
  },
  methods: {

  }
}
</script>

<style lang="scss" scoped>
.locations{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 50px;
  .lisboa,
  .barreiro{
    width: 100px;
    height: 100px;
    color: black;
    margin: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    a{
      text-decoration: none;
    }
  }
}

.container-restaurant{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurant__list{
    display: flex;
    flex-direction: column;
    button{
        align-items: center;
        width: 300px;
        height: 50px;
        border-radius: 5px;
        font-size: 18px;
        margin: auto;
        &:hover{
          color: white;
          background-color: black;
        }
    }
    &__random{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 80px;
      width: 400px;
      margin: auto;
      margin-top: 30px;
      margin-bottom: 30px;
      background: #ffd7b4;
      display: flex;
      flex-direction: column;

      p{
        font-size: 18px;
      }
      #restaurant__list__random__result{
        font-size: 25px;
        color: black;
      }
  }
    &__unique{
      margin: 20px;
      padding: 30px;
      padding-left: 50px;
      width: 400px;
      background: #c0dcdc;
      display: flex;
      &:hover{
        box-shadow: 3px 5px 62px -13px #848686;
      }
      .restaurant-text-container{
        width: 60%
      }
      a{
        text-decoration: none;
        color: black;
      }
      &__text{
        display: flex;
        font-size: 18px;
        align-items: center;
        p{
          font-weight: bold;
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }
  }
}
.view-restaurant-btn{
  padding: 0 15px 0px 15px;
  height: 40px;
  background: #f0b7b7;
  align-items: center;
  display: flex;
  justify-content: center;

}
.new-restaurant-btn{
  width: 200px;
  height: 50px;
  background: lightblue;
  display: flex;
  align-items: center;
  justify-content: center;
  a{
    text-decoration: none;
    color: black;
  }
}
.back-link{
  text-decoration: none;
  color: white;
}
.back-btn{
  width: 150px;
  height: 30px;
  background: rgb(109, 109, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}
@media only screen and (max-width: 500px) {
  .restaurant--wrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .restaurant__list{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 75%;
      .restaurant__list__unique{
        width: 100%;
        padding: 30px;
        .restaurant-text-container{
          .restaurant__list__unique__text{
            font-size: 16px;
          }
        }
        .view-restaurant{
          width: 100px;
          font-size: 14px;
        }
      }
    }
  }
}
</style>



