<template>
  <div id="view-restaurant">
    <ul>
      <li>
        <h4>{{name}}</h4>
        <div class="restaurant-infos">
          <p>{{zomato_link}}</p>
          <p>{{price}}</p>
          <p>{{location}}</p>
          <p>{{obs}}</p>
        </div>
      </li>
    </ul>
    <div class="action-buttons">
      <router-link class="back-btn" to="/">Regressar</router-link>
      <router-link class="edit-btn" v-bind:to="{name: 'edit-restaurant', params: {name:name}}">Editar</router-link>
    </div>

  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-restaurant',
  data (){
    return{
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null
    }
  },
  beforeRouteEnter(to, from, next){
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
        })
      })
    })
  },
  watch:{
    '$route': 'fetchData'
  },
  methods: {
    fetchData(){
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.price = doc.data().price
        })
      })
    }
  }
}
</script>

<style scoped lang='scss'>
#view-restaurant{
    display: flex;
    margin: auto;
    margin-top: 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #c0dcdc;
    width: 30%;
    padding: 40px;
    ul{
      li{
        h4{
          font-size: 30px;
          padding: 10px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

        }
        .restaurant-infos{
          padding: 10px;
          p{
            font-size: 20px;
            font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
          }
        }
      }
    }

}
.action-buttons{
  display: flex;
  .back-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #36544c;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    color: white;
    margin: 5px;
  }
  .edit-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ea6060;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    color: white;
    margin: 5px;
  }
}
@media only screen and (max-width: 500px) {
  #view-restaurant{
    width: 55%;
  }
}
</style>



